"use client";

import { useParams, useRouter } from "next/navigation";
import { useEffect, useState } from "react";

export default function JobDetails() {
    const { id } = useParams();
    const router = useRouter();


    const jobs = [
        {
            id: 1,
            title: "Frontend Developer",
            company: "CodeHaven",
            location: "Remote - Nigeria",
            type: "Full-time",
            description:
                "We are seeking a passionate Frontend Developer skilled in React.js and Tailwind CSS to join our fully remote team. You‚Äôll be responsible for building beautiful, responsive web interfaces and collaborating with backend engineers to deliver a smooth user experience.",
            requirements: [
                "2+ years experience with React.js",
                "Good understanding of Tailwind CSS",
                "Strong communication and teamwork skills",
            ],
            salary: "‚Ç¶300,000 - ‚Ç¶400,000 / month",
        },
        {
            id: 2,
            title: "Backend Developer",
            company: "TechHive",
            location: "Remote - Nigeria",
            type: "Contract",
            description:
                "Looking for a Node.js developer experienced in REST APIs and MongoDB. Join our team to work on scalable backend systems for global clients.",
            requirements: [
                "3+ years experience with Node.js",
                "Familiarity with MongoDB and Express.js",
                "Ability to work independently",
            ],
            salary: "‚Ç¶250,000 - ‚Ç¶350,000 / month",
        },
    ];

    const [job, setJob] = useState(null);

    useEffect(() => {
        // Simulate fetching from backend
        const foundJob = jobs.find((j) => j.id === parseInt(id));
        setJob(foundJob);
    }, [id]);

    if (!job) {
        return (
            <div className="flex flex-col items-center justify-center min-h-screen text-gray-600">
                <p>Loading job details...</p>
            </div>
        );
    }

    return (
        <section className="max-w-3xl mx-auto py-12 px-6">
            <button
                onClick={() => router.back()}
                className="text-blue-600 hover:underline mb-6"
            >
                ‚Üê Back to jobs
            </button>

            <div className="bg-white shadow-md rounded-2xl p-8">
                <h1 className="text-3xl font-bold text-gray-900 mb-2">{job.title}</h1>
                <p className="text-gray-600 mb-4">
                    {job.company} ‚Ä¢ {job.location} ‚Ä¢ {job.type}
                </p>

                <div className="mb-4">
                    <h2 className="text-lg font-semibold text-gray-800 mb-2">Description</h2>
                    <p className="text-gray-700 leading-relaxed">{job.description}</p>
                </div>

                <div className="mb-4">
                    <h2 className="text-lg font-semibold text-gray-800 mb-2">Requirements</h2>
                    <ul className="list-disc list-inside text-gray-700 space-y-1">
                        {job.requirements.map((req, index) => (
                            <li key={index}>{req}</li>
                        ))}
                    </ul>
                </div>

                <div className="flex items-center justify-between mt-6">
                    <p className="font-medium text-gray-900">
                        üí∞ <span className="text-blue-600">{job.salary}</span>
                    </p>
                    <button className="bg-blue-600 text-white px-5 py-2 rounded-lg hover:bg-blue-700 transition">
                        Apply Now
                    </button>
                </div>
            </div>
        </section>
    );
}
